'use strict'

import axios from 'axios'
import logo from './logo'

axios.defaults.baseURL='http://lisfes.cn'

export const xhr = (param, url, type = 'GET') => {
    return new Promise((resolve, reject) => {
        if (type = 'GET')
            axios.get(url, param).then(res => {
                resolve(res.data)
            }).catch(e => {
                reject(e)
            })
        else
            axios.post(url, param.params).then(res => {
                resolve(res.data)
            }).catch(e => {
                reject(e)
            })

    })
}


//ËØ∑Ê±ÇÊã¶Êà™Âô®
axios.interceptors.request.use(function (config) {
    // Âú®ÂèëÈÄÅËØ∑Ê±Ç‰πãÂâçÂÅö‰∫õ‰ªÄ‰πà
    return config;
}, function (error) {
    // ÂØπËØ∑Ê±ÇÈîôËØØÂÅö‰∫õ‰ªÄ‰πà
    return Promise.reject(error)
})

export const all = Arr => {
    return Promise.all(Arr)
}


//ÂìçÂ∫îÊã¶Êà™Âô®
axios.interceptors.response.use(
    res => {
        logo(res.request, res)
        return res
    },
    err => {
        logo(err.request, err.response)
        return err
    }
)






//ËΩÆÊí≠Âõæ
export const getbanner = param => {
    return xhr(param, `/banner`)
}

//Êé®ËçêÊ≠åÂçï
export const getlized = param => {
    return xhr(param, `/personalized`)
}

//Ê≠åÂçïËØ¶ÊÉÖ
export const getplaylistdet = param => {
    return xhr(param, `/playlist/detail`)
}

//Ëé∑ÂèñÊ≠åÊâã
export const getSingerlist = param => {
    return xhr(param, `/top/artists`)
}










